.section {
  position: relative; }

.sectionBox {
  position: relative;
  padding: 5rem 2rem;
  max-width: 60rem;
  margin: 0 auto; }

.sectionContent h1 {
  font-size: 3rem;
  font-family: "Montserrat", "Gothic A1", sans-serif; }

.sectionContent hr {
  display: inline-block;
  width: 4rem;
  height: 0.2rem;
  background-color: currentColor; }
