.hero {
  position: relative;
  font-family: "Montserrat", "Gothic A1", sans-serif;
  color: #eee;
  text-align: center; }
  .hero hr {
    margin: 0 auto;
    width: 3em;
    border-bottom: 2px solid currentColor; }
  .hero h1 {
    font-size: 3em;
    font-weight: 700; }
  .hero h2 {
    font-size: 2em;
    font-weight: 400; }
  .hero strong {
    color: #6dcff6; }
  .hero table {
    width: 100%;
    max-width: 30em;
    margin: 0 auto;
    border-collapse: collapse; }
    .hero table th {
      width: 5em; }
    .hero table tbody tr:first-child th,
    .hero table tbody tr:first-child td {
      border-top: 3px solid #eee; }
    .hero table tbody tr:last-child th,
    .hero table tbody tr:last-child td {
      border-bottom: 3px solid #eee; }
    .hero table tbody th,
    .hero table tbody td {
      border-top: 1px solid #eee;
      border-bottom: 1px solid #eee;
      padding: 0.5rem 1rem; }
    .hero table tbody td {
      text-align: left; }
    .hero table tbody * + th,
    .hero table tbody * + td {
      border-left: 1px solid rgba(238, 238, 238, 0.1); }

.heroDiagonal {
  top: -3vw; }

.heroBox {
  position: relative;
  margin: 0 auto;
  padding: 8rem 2rem;
  max-width: 60rem; }

.heroBg {
  width: 100%;
  height: 100%;
  background-color: #242d68;
  background-image: url("../assets/images/hero-bg.jpg");
  background-position: center center;
  background-size: cover; }
  .heroBg:before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(36, 45, 104, 0.5);
    background-image: radial-gradient(#242d68, transparent); }

.introduce {
  color: #222;
  text-align: center; }

.introduceDiagonal {
  background-color: #eee; }

.goals {
  color: #eee;
  text-align: justify; }

.goalsDiagonal {
  background-color: #4a56a8; }

.about {
  color: #eee;
  text-align: justify; }

.aboutDiagonal {
  background-color: #242d68; }
