.wrap {
  display: inline-block;
  width: 3.5em;
  height: 6em;
  border-radius: 100em;
  background-color: #4a56a8;
  padding: 1em;
  box-shadow: 0 0.5em 1em rgba(36, 45, 104, 0.66); }

.wheel {
  display: block;
  width: 0.3em;
  height: 1.33em;
  background-color: #eee;
  border-radius: 100em;
  margin: 0 auto; }
