*,
*:before,
*:after {
  box-sizing: border-box; }

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.05; }

a {
  color: inherit;
  text-decoration: none; }

@media screen and (max-width: 450px) {
  html {
    font-size: 3.5vw; } }

@media screen and (min-width: 450px) {
  html {
    font-size: 16px; } }

body {
  font-family: "Gothic A1", sans-serif;
  background-color: #222;
  color: #eee;
  line-height: 1.58;
  overflow-x: hidden; }

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3000;
  padding: 0;
  text-align: right;
  padding: 0.5rem 1.5rem;
  color: white; }
  .header:after {
    content: "";
    display: table;
    width: 100%; }

.headerDiagonal {
  background-image: linear-gradient(to bottom, #1d2455, rgba(36, 45, 104, 0)); }

.headerLeft {
  position: relative;
  float: left; }

.headerRight {
  position: relative;
  float: right; }

.headerLogo {
  display: inline-block;
  width: 10rem;
  padding: 1rem 0.5rem; }
  .headerLogo img {
    display: block;
    width: 100%; }

.headerLink {
  cursor: pointer;
  display: inline-block;
  padding: 1rem 0.4rem 0.9rem;
  margin: 0 0.2rem;
  border-bottom: 0.1rem solid transparent;
  transition: color 0.15s ease, border-color 0.15s ease; }
  .headerLink:hover, .headerLink.active {
    border-color: currentColor; }
  .headerLink.active {
    color: #6dcff6;
    font-weight: 700; }

.main {
  min-height: 100vh; }

.footer {
  position: relative;
  color: #eee; }

.footerDiagonal {
  background-color: #222; }

.footerBox {
  position: relative;
  margin: 0 auto;
  width: 100%;
  max-width: 60rem;
  padding: 6rem 2rem; }
  .footerBox:after {
    content: "";
    display: table;
    width: 100%; }
  @media screen and (max-width: 40rem) {
    .footerBox {
      text-align: center; } }

@media screen and (min-width: 40rem) {
  .footerLeft {
    float: left; } }

@media screen and (min-width: 40rem) {
  .footerRight {
    float: right; } }
